import   XLSX  from "xlsx";
//xlsx
/**
 * excel
 * @param {*} file 
 */
const importsExcel=(file)=>{
    //promise
  return  new Promise((resolve,reject)=>{
     // 
     const { files } = file.target; //
     // FileReader
     const fileReader = new FileReader();
 
     fileReader.onload = event => { //  excel
         try {
             const { result } = event.target;
             // excel
             const workbook = XLSX.read(result, { type: 'binary' });
             let data = []; // 
             // 
             for (const sheet in workbook.Sheets) {
                 if (workbook.Sheets.hasOwnProperty(sheet)) {
                      data = data.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]));
                 }
             }
             resolve(data);//
         } catch (e) {
             // 
            reject("");//
         }
     };
     // 
     fileReader.readAsBinaryString(files[0]);
  })
 
}
/**
 * excel
 * @param {*} headers 
 * @param {*} data 
 * @param {*} fileName 
 */
const  exportExcel =(headers, data, fileName = 'demo.xlsx')=>{
 
    const _headers = headers
    .map((item, i) => Object.assign({}, { key: item.key, title: item.title, position: String.fromCharCode(65 + i) + 1 }))
    .reduce((prev, next) => Object.assign({}, prev, { [next.position]: { key: next.key, v: next.title } }), {});
const _data = data
    .map((item, i) => headers.map((key, j) => Object.assign({}, { content: item[key.key], position: String.fromCharCode(65 + j) + (i + 2) })))
    // 
    .reduce((prev, next) => prev.concat(next))
    //  worksheet 
    .reduce((prev, next) => Object.assign({}, prev, { [next.position]: { v: next.content } }), {});
 
//  headers  data
const output = Object.assign({}, _headers, _data);
// 
const outputPos = Object.keys(output);
//  ,["A1",..., "H2"]
const ref = `${outputPos[0]}:${outputPos[outputPos.length - 1]}`;
 
//  workbook 
const wb = {
    SheetNames: ['mySheet'],
    Sheets: {
        mySheet: Object.assign(
            {},
            output,
            {
                '!ref': ref,
                '!cols': [{ wpx: 45 }, { wpx: 100 }, { wpx: 200 }, { wpx: 80 }, { wpx: 150 }, { wpx: 100 }, { wpx: 300 }, { wpx: 300 }],
            },
        ),
    },
};
 
//  Excel
XLSX.writeFile(wb, fileName);
 
}
 
 
export  {
    importsExcel,
    exportExcel
}